import QQMapWX from '@/common/utils/qqmap-wx-jssdk.min.js'
import modal from "@/components/modal/modal"
import tuiIcon from "@/components/icon/icon"
import md5 from "@/common/utils/md5.js"
import tuiCountdown from "@/components/countdown/countdown"
import {
	ajax,
	getSecret
} from '@/common/ajax/ajax.js'
export default {
	components: {
		modal,
		tuiIcon,
		tuiCountdown
	},
	data() {
		return {
			getheight: '',
			specialImgHeight: '',
			storeName: '总部旗舰店',
			address: '',
			city: '',
			id: '',
			secret: {},
			showHeader: true,
			afterHeaderOpacity: 1, //不透明度
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			nVueTitle: null,
			currentSwiper: 0,
			// 轮播图片
			swiperList: [],
			// 活动图片
			actionBanner: [],
			// 分类菜单
			categoryList: [],
			// 限时抢购
			seckillList: [],
			seckillTime: 0,
			bargainList: [],
			Promotion: [],
			//猜你喜欢列表
			productList: [],
			// 活动
			specialList: [],
			loadingText: '到底了',
			amapPlugin: null,
			// 定位弹窗
			modalShow: false,
			storeItems: [{
				store_name: '总部旗舰店',
				desc: '由总部统一发货',
				store_code: '10001'
			}],
			// 门店编码
			store: '10001',
			hint: '熙美诚品',
			popup: {},
			// 向左滑动距离
			scrollLeft: 0,
			// 弹窗高度
			popHeight: 0

		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		this.getgoods();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onLoad() {
		this.getbig();
		this.getgoods();			
		this.getLocation();

	},
	onShow() {
		// this.onLoad();
		console.log(uni.getStorageSync('store'));
		if (uni.getStorageSync('store')) {
			this.storeName = uni.getStorageSync('store').store_name;
			this.store = uni.getStorageSync('store').store_code
			this.getgoods();

		}

	},
	methods: {
		// 获取商品总分类
		getbig() {
			let timestamp = (new Date()).getTime();
			let secret = md5.hex_md5("XM#" + md5.hex_md5(String(timestamp)) + "@secret");
			let data = {
				store: this.store,
				secret: timestamp,
				timestamp: secret
			}
			ajax({
				url: '/Procate/getBig',
				method: 'post',
				data: data,
				success: (res) => {
					console.log(res);
					this.categoryList = res.data.splice(1, 10);
					console.log(this.categoryList)
				}
			})
		},
		// this.$xm.post('/Procate/getBig',data,(res)=>{
		// 	console.log(res);
		// 	this.categoryList=res.splice(1,10);
		// 	console.log(this.categoryList)
		// })

		getLocation() {
			uni.getLocation({
				success: (res) => {
					this.getCity(res.latitude, res.longitude) //获取当前所在城市						
				},
				fail: (res) => {
					console.log(res);

				}
			})
		},
		// 获取定位城市
		getCity(latitude, longitude) {
			let demo = new QQMapWX({
				key: 'LVHBZ-SOY6W-Z5URP-OC34Q-CLWZH-Y6BCX' // 开发密钥（key）
			});
			demo.reverseGeocoder({
				location: {
					latitude: latitude,
					longitude: longitude
				},
				success: (res) => {
					console.log(res);
					this.city = res.result.ad_info.city;
					let latitude = res.result.location.lat;
					let longitude = res.result.location.lng;
					this.address = res.result.address
					uni.setStorageSync('local', res.result.address);
					// this.getgoods()
					let params = {
						city: this.city,
						from: latitude + ',' + longitude
					}
					this.$xm.post('/Index/getStoreList', params, (res) => {
						this.storeItems = res.data;
						uni.setStorageSync('storeList', res.data)

					})
				},
				fail: function(res) {
					console.log(res)

				},
				complete: function(res) {

				}
			});
		},
		// 获取商品
		getgoods() {
			this.swiperList = []
			let timestamp = (new Date()).getTime();
			let secret = md5.hex_md5("XM#" + md5.hex_md5(String(timestamp)) + "@secret");
			let data = {
				store: this.store,
				secret: timestamp,
				timestamp: secret
			}
			ajax({
				url: '/Index/getInfo',
				method: 'post',
				data: data,
				success: (res) => {
					let result = res.data;
					console.log(result);
					this.popup = result.popup;
					this.popHeight = this.popup.height;
					if (uni.getStorageSync('store')) {
						this.modalShow = false;
					} else {
						if (result.popup.img != '') {
							this.modalShow = true;
						} else {
							this.modalShow = false;
						}
					}
					this.getheight = result.banner[0].height + 'rpx';
					this.specialImgHeight = result.special.height + 'rpx';
					this.hint = result.hint
					uni.setNavigationBarTitle({
						title: this.hint
					});
					this.swiperList = result.banner;
					this.swiperList.map(ele => {
						ele.img = result._URL_ + ele.img
					})
					this.actionBanner = result.img;

					result.faddish.forEach((ele) => {
						ele.itemcount = 0;
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';

					})
					this.Promotion = result.faddish;
					this.bargainList = result.bargain;
					this.productList = result.rem;
					this.bargainList.forEach((ele) => {
						ele.itemcount = 0;
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					})
					this.productList.forEach((ele) => {
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
						ele.itemcount = 0;
						if (ele.pro_name.length > 8) {}
					})
					result.special.goods.map((ele) => {
						ele.itemcount = 0;
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					})
					this.specialList = result.special;
					this.seckillList = result.seckill;
					this.seckillList.forEach((ele) => {
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					})
					this.seckillTime = parseInt(result.seckill[0].etime) - Math.round(new Date() / 1000);

					console.log(this.seckillTime);

				}
			})



		},

		scroll(e) {
			this.scrollLeft = e.detail.scrollLeft;
		},
		//搜索跳转
		toSearch() {
			uni.navigateTo({
				url: '../search/search/search'
			})

		},
		// 跳转宠物页
		toPet() {
			// uni.navigateTo({
			// 	url: '../pet/pet/pet'
			// })
		},
		//轮播图跳转
		toSwiper(e) {
			let action = e.src.split('@')
			switch (action[0]) {
				case 'product':
					uni.navigateTo({
						url: '../goods/goods?gid=' + action[1]
					})
					break;
				case 'news':
					uni.navigateTo({

					})
					break;
				case 'sort':

					break;
				case 'player':
					uni.navigateTo({
						url: "plugin-private://wx2b03c6e691cd7370/pages/live-player-plugin?room_id=" + action[1]
					})
					break;
				case 'open':
					uni.navigateTo({
						url: "../webViwe/webViwe?url=" + action[1]
					})
					break;
				default:

			}
		},
		// 活动跳转
		toPage(e) {
			console.log(e);
			let action = e.url.split('@');
			console.log(action)
			switch (action[0]) {
				case 'product':
					uni.navigateTo({
						url: '../goods/goods?gid=' + action[1]
					})
					break;
				case 'news':
					// uni.navigateTo({

					// })
					break;
				case 'sort':

					break;
				case 'player':
					uni.navigateTo({
						url: "plugin-private://wx2b03c6e691cd7370/pages/live-player-plugin?room_id=" + action[1]
					})
					break;
				case 'open':
					console.log(1)
					uni.navigateTo({
						url: '../webViwe/webViwe/webViwe?url=' + action[1]
					})
					break;
				default:

			}

		},
		//分类跳转
		toCategory(e) {
			uni.setStorageSync('cateSelect', e.id);
			uni.switchTab({
				url: '../tabBar/category/category'
			})

		},
		//商品跳转
		toGoods(e) {
			uni.navigateTo({
				url: '../goods/goods?gid=' + e.id
			});
		},
		// 秒杀跳转
		toSeckill() {
			uni.navigateTo({
				url: '../seckill/seckill/seckill'
			})
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		tobuy(e) {
			e.itemcount++;
			this.id = e.id;

			this.joinCart();
			// this.toConfirmation();
		},
		// 加入购物车
		joinCart() {
			let data = {
				proid: this.id,
				pronum: 1,
			};
			this.$xm.post('/Cart/add', data, (res) => {
				let result = res.msg;
				if (result == '加入购物车成功') {
					uni.showToast({
						title: "已加入购物车"
					});
				}
				this.$xm.post('/Cart', '', res => {
					uni.setStorage({
						key: 'cart',
						data: res.count,
						success: (res) => {
							uni.getStorage({
								key: 'cart',
								success: (res) => {
									let count = res.data;
									if (res.data !== null) {
										count = count.toString();
										uni.setTabBarBadge({
											index: 2,
											text: count
										})
									}
								}
							})
						}
					})
				})
			})

		},
		//跳转确认订单页面
		toConfirmation() {
			uni.navigateTo({
				url: '../order/confirmation'
			})
		},
		// 跳转到所有门店页面
		toStore() {
			uni.navigateTo({
				url: '../store/store/store'
			})
		},
		// 跳转活动
		toactivity() {

		}
	}
};
